@import url(https://fonts.googleapis.com/css?family=DM+Serif+Display|Karla:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
header {
  padding: 3rem 0;
}

header nav {
  display: flex;
  justify-content: space-between;
  align-items: center; 
}

/* mobile first */
.nav-items-desktop {
  display: none; 
}

.dropdown-btn {
  cursor: pointer;
}

nav ul li {
  margin: 30px 0;
}

/* dropdown menu */
.dropdown-menu {
  z-index: 3;
  height: 100vh;
  width: 100%;
  position: relative;
  top: 0;
  padding-top: 6rem;
  background-color: var(--very-deep-purple);
  color: #fff;
  background-image: url(/insure-lp-fem/static/media/bg-pattern-mobile-nav.2922ed2c.svg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;
  overflow: hidden; 

}

.dropdown-menu .nav-items .nav-item {
  font-size: 2rem;
  line-height: 2;
  letter-spacing: 1.36px;
  cursor: pointer;
  text-transform: uppercase;
}
.dropdown-menu .nav-items .nav-item:last-child {
  padding: 0.8rem 0;
  width: 100%;
}

.dropdown-menu .nav-items .nav-item:not(:last-child) {
  margin-bottom: 2.4rem;
}

/* between tablet and small laptop */

@media screen and (min-width: 960px) {
  .intro-img-mobile {
    max-width: 100%;
  }

  .dropdown-btn {
  display: none;
  }

  /* display none to display flex at breakpoint */
  .nav-items-desktop {
    display: flex; 
    flex-direction: row; 
    align-items: center; 
  }

  .nav-items-desktop ul {
    display: flex; 
    flex-direction: row; 
  }

  .nav-items-desktop .nav-item {
    text-transform: uppercase;
    font-size: 2rem;
    letter-spacing: 1.5px;
    cursor: pointer;
    color: var(--gray-purple);
    transition: all 0.3s ease;
  }

  .nav-item:not(:last-child) {
    margin-right: 2.5rem;
  }

  .nav-item:not(:last-child):hover {
    color: var(--very-deep-purple);
  }

}

