/* Custom Variables */

:root{
  /* color palette */
  --deep-purple: hsl(256, 26%, 20%);
  --very-deep-purple: hsl(270, 9%, 17%);

  --gray-purple: hsl(273, 4%, 51%);
  --gray-blue:  hsl(216, 30%, 68%);
  --gray-white: hsl(0, 0%, 98%);

  /* 1 rem = 10px, easier to convert between units */
  font-size: 62.5%;

  /* font weight and size */
  --font-size: 2rem;
  --font-weight-normal: 400;
  --font-weight-bold: 700;

  /* fonts */
  --body-font-family: 'Karla', sans-serif;
  --heading-font-family:'DM Serif Display', serif;
}

/* Reset  */
* {
  padding: 0; 
  margin: 0; 
  border-style: border-box;
  font-family: inherit;
}

body {
  font-size: 2rem;
  font-size: var(--font-size);
  font-family: 'Karla', sans-serif;
  font-family: var(--body-font-family);
  font-weight: 400;
  font-weight: var(--font-weight-normal);

  /* mobile first design */
  text-align: center; 
}


h1, h2, h3, h4, h5 {
  font-family: 'DM Serif Display', serif;
  font-family: var(--heading-font-family);
  font-weight: 400;
  font-weight: var(--font-weight-normal);
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

hr {
  border: none;
}

/* buttons */
.btn-light {
  font-size: 1.8rem;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    border: 1px solid #fff;
    padding: 1.3rem 101.7px;
    transition: all 0.3s ease;
    color: #fff;
}

.btn-light:hover {
  background-color: #fff;
  color: hsl(256, 26%, 20%);
  color: var(--deep-purple);
}


.btn-dark  {
  color: hsl(270, 9%, 17%);
  color: var(--very-deep-purple);
  padding: 1.3rem 2rem;
  border: 1px solid hsl(270, 9%, 17%);
  border: 1px solid var(--very-deep-purple);
}

.btn-dark:hover {
  color: #fff;
  background-color: hsl(270, 9%, 17%);
  background-color: var(--very-deep-purple);
}

/* wrapper */
.container {
  padding: 0 2.4rem;
  min-width: 320px;
  box-sizing: border-box;
}

.col {
  width: 100%;
}

.d-none {
  display: none; 
}

/* lines */

.dark-hr {
  max-width: 150px;
  height: 1px;
  background-color: hsl(273, 4%, 51%);
  background-color: var(--gray-purple);
  margin: 0 auto;
  margin-bottom:4rem;
}

.white-hr {
  display: none;
}

.long-hr {
  width: 100%;
  height: 1px;
  background-color: rgb(180, 180, 180); 
}

@media screen and (min-width: 960px) {
  .dark-hr {
    margin: 6rem 0;
  }
}


@media screen and (min-width: 1200px) {
  
  .container {
    padding: 0 10rem;
  }


  .white-hr {
    display: block;
    height: 1px;
    max-width: 150px;
    background-color: hsl(0, 0%, 98%);
    background-color: var(--gray-white);
    margin-bottom: 6.3rem;
  }

}

@media screen and (min-width: 1440px) {
  body {
    text-align: left;
  }

  .container {
    max-width: 1440px;
    padding: 0 16.5rem;
    margin: 0 auto;
  }

  .col {
    width: 49%;
  }

  .line {
    margin: unset;
    margin-bottom: 6.3rem;
  }


}



